import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AnySoaRecord } from 'dns';


const httpOption = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
  }),
};
@Injectable({
  providedIn: 'root'
})
export class ApiProductosService {

  url: string = "https://lideart.com.mx/Automatizacion/listaJson.php"

  constructor(public http: HttpClient) {}

  getProductos() {
    return this.http.get<Response>(this.url);
  }
  getSpecificProduct(id: number) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/listaJson2.php?id="+id);
  }

  getCategorias(id_cat: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaCategorias.php?id_cat="+id_cat);
  }

  getRelacionados(id_cat: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/productosRelacionados.php?id_cat="+id_cat);
  }


  checaCupon(datos:any=[]){
    return this.http.post<Response>('https://lideart.com.mx/Automatizacion/apis/checaCupon.php',datos);
  }

  borraCupon(datos:any=[]){
    return this.http.post<Response>('https://lideart.com.mx/Automatizacion/apis/cancelaCupon.php',datos);
  }

  getDescripcionProducto(id_product: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaArticulo.php?id_product="+id_product);
  }

  getSubcategoriaPorProducto(id_padre: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaSubCategorias.php?id_parent="+id_padre);
  }
  getSubcategoriaPorProducto2() {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/ejemplos.php");
  }

  getDescripcionPaquete(id_product: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaArticuloPaquete.php?id_product="+id_product);
  }

  getDatosPedido(id_customer: any){
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaPedidos.php?id_customer="+id_customer);
  }

  getSlaidaHome(){
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/traesliderIMG.php");
  }

  getSliderImgs(id_product: any){
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/getImgSlider.php?id_product="+id_product);
  }

  getVideos(id_product: any){
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/traeVideos.php?id_product="+id_product);
  }

  getPrecioUnitario(objetoPost: any){
    return this.http.post<any>('https://lideart.aytcloud.com/inaxLideartExpoConsAPI/api/Product/getPrecio',objetoPost);
  }

  getExistencias(reference: string, sitio:string){
    return this.http.get<any>('https://lideart.aytcloud.com/inaxLideartExpoConsAPI/api/Product/getExistencias/'+reference+'/CHIH');
  }

    getDescuento(datos:any=[]){
      return this.http.post<Response>('https://lideart.com.mx/Automatizacion/apis/checaDescuento.php',datos);
  }

  getInfoPaquetes(id_product: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/consultaInfoPaquetes.php?id_product="+id_product);
  }

   esPaquete(id_product: any) {
    return this.http.get<Response>("https://lideart.com.mx/Automatizacion/apis/esPaquete.php?id_product="+id_product);
  }


  
}
