          backdrop-filter: blur(50px);
}

ion-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

ion-card {
  background: #fdfdfd;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
ion-card .where {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
ion-card .where a {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  font-weight: 600;
  color: #000;
}
ion-card .where a span {
  display: inline-block;
  border-bottom: 3px solid #14A8CD;
  padding-bottom: 0.5rem;
}
ion-card .where #regis {
  font-weight: 600;
  color: #14A8CD;
}
ion-card img {
  width: 60%;
  padding: 1rem;
}
ion-card form {
  width: 100%;
  height: auto;
}
ion-card .error {
  color: red;
  font-size: 1rem;
  font-weight: bolder;
}
ion-card .correcto {
  color: green;
  font-size: 1rem;
  font-weight: bolder;
}
ion-card ion-item {
  --ion-item-background: none !important;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}
ion-card ion-button {
  height: 4rem;
  font-family: "Roboto", sans-serif;
  font-size: 0.8rem;
  letter-spacing: 0.1rem;
  font-weight: 600;
  --background: #14A8CD;
  color: #fff;
}
ion-card ion-button:hover {
  --background: #14A8CD!important;
  color: #fff;
  transition: 1s;
}
ion-card ion-input {
  color: #000;
  font-weight: 600;
  /*--placeholder-color: #000;
  --placeholder-weight: 600;*/
}
ion-card .custom-placeholder {
  color: #000;
  font-size: 1.1rem;
  font-weight: bolder;
}
ion-card ion-label {
  color: black;
  font-weight: 600;
}
ion-card .item-has-focus .label-floating.sc-ion-label-md-h:not(.ion-color) {
  color: #000;
}
ion-card .item-interactive.item-has-focus {
  --highlight-background: #000;
}
ion-card .forgot {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
ion-card .forgot a {
  font-size: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: #000;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
}
ion-card ion-text {
  color: #000;
}
ion-card ion-list {
  background: none !important;
}
ion-card ion-list ion-item {
  --background: none !important;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-left: 0;
  padding-right: 0;
}
ion-card ion-list ion-item ion-checkbox {
  width: 1.2rem;
  height: 1.2rem;
  --border-radius: .6rem;
  text-align: center;
}
ion-card ion-list ion-item ion-label {
  margin-left: 1rem;
  white-space: normal !important;
  text-align: center;
}

.mod {
  --background: black;
}

#closeModal {
  font-size: 1.2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  color: #000;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
}

@media (min-width: 992px) {
  ion-card {
    width: 30em;
  }
  ion-card form ion-item {
    margin-top: 0rem;
    margin-bottom: 0rem;
    margin: 1em 0em;
  }
  ion-card form ion-item ion-toggle {
    padding: 1em 0em;
  }
  ion-card form ion-item ion-label {
    font-weight: 100;
    font-size: 0.9em;
  }
  ion-card ion-list .vrp {
    text-decoration: none;
    font-size: 1.1em;
  }
  ion-card ion-button {
    height: 4em;
    font-family: "Roboto", sans-serif;
    font-size: 0.9em;
    /* letter-spacing: 0.1rem; */
    font-weight: 100;
    --background: #14A8CD;
    color: #fff;
  }
  ion-card ion-text h1 {
    font-size: 1.7em;
    font-weight: 100;
  }
  ion-card .forgot #closeModal {
    margin-top: 0rem;
    margin-bottom: 0rem;
    margin: 1em 0em;
    transition: 0.4s all ease-in-out;
    font-size: 1.1em;
  }
  ion-card .forgot #closeModal:hover {
    color: #14A8CD;
  }
}
.modalRecuperarCss::-webkit-scrollbar {
  width: 0.35em;
  background-color: transparent;
  height: 0.7em;
}

.modalRecuperarCss::-webkit-scrollbar-thumb {
  background-color: rgba(0, 162, 199, 0.4745098039);
