  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
ion-card .where a {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  font-weight: 600;
  color: #000;
}
ion-card .where a span {
  display: inline-block;
  border-bottom: 3px solid #14A8CD;
  padding-bottom: 0.5rem;
}
ion-card .where #regis {
  font-weight: 600;
  color: #14A8CD;
}
ion-card img {
  width: 60%;
  padding: 1rem;
}
ion-card form {
  width: 100%;
  height: auto;
}
ion-card ion-item {
  --ion-item-background: none !important;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding-left: 2rem;
  padding-right: 2rem;
}
ion-card ion-button {
  height: 4rem;
  font-family: "Roboto", sans-serif;
  font-size: 0.8rem;
  letter-spacing: 0.1rem;
  font-weight: 600;
  --background: #14A8CD;
  color: #fff;
}
ion-card ion-button:hover {
  --background: #14A8CD!important;
  color: #fff;
  transition: 1s;
}
ion-card ion-input {
  color: #000;
  font-weight: 600;
}
ion-card ion-label {
  color: #000;
  font-weight: 600;
}
ion-card .item-has-focus .label-floating.sc-ion-label-md-h:not(.ion-color) {
  color: #000;
}
ion-card .item-interactive.item-has-focus {
  --highlight-background: #000;
}
