  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
ion-card .forgot a {
  font-size: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: #000;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
}

.mod {
  --background: black;
}

#closeModal {
  font-size: 1.2rem;
  margin-top: 2rem;
